html {
  --highlight-color: #00acdc;
  --main-color: #000;
  font-family: Roboto, sans-serif;
  font-size: 12px;
}

body {
  margin: 0;
}

.header__logo {
  justify-content: center;
  width: 40px;
  height: 40px;
  display: flex;
}

.header {
  justify-content: space-between;
  align-items: center;
  margin: 0;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.nav__list {
  justify-content: space-between;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  margin: 0 0 0 20px;
}

.nav__link {
  color: var(--main-color);
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: block;
  position: relative;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__link:hover, .is-active {
  color: var(--highlight-color);
}

.is-active:after {
  content: "";
  background-color: var(--highlight-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}
